.list {
  padding-left: 26px;
  li {
    @include body;
    position: relative;
    @media (max-width: 767px) {
      animation-delay: 0s !important;
    }
    &::before {
      content: '';
      position: absolute;
      top: 6px;
      left: -20px;
      width: 8px;
      height: 8px;
      background-color: $blue;
      @media (max-width: 767px) {
        top: 4px;
      }
    }
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
}